.Body{
  background-color: rgb(95, 89, 89);
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
h1{
  font-size: 60px;
  font-weight: bolder;
  color: white;
}
p{
  font-size: 40px;
  font-weight: 500;
  color: white;
}
button{
  background-color: rgb(255, 255, 255);
  border: none;
  border-radius: 5px;
  margin: 0 10px;
  padding: 10px 20px;
  font-size: 20px;
  font-weight: 500;
  cursor: pointer;
}
.btn{
  display: flex;
  justify-content: space-between;
  margin: 0 10px;

}

button:hover{
  background-color: rgb(0, 0, 0);
  border: 1px solid rgb(255, 255, 255);
  color: rgb(255, 255, 255);
  border-radius: 5px;
}
